.welcome-container {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
  }
  
  .bg-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
    opacity: 0.7;
  }
  
  .welcome-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: white;
  }
  
  .welcome-title {
    font-size: 3rem;
    margin-bottom: 1rem;
  }
  
  .welcome-text {
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }
  
  .welcome-link {
    font-size: 1.2rem;
    background-color: #ff4500;
    color: white;
    padding: 0.5rem 1rem;
    border-radius: 0.3rem;
    text-decoration: none;
  }
  
  .welcome-link:hover {
    background-color: #ff5500;
  }
  